import React from 'react';
import '../../App.css';
import HeroSection from '../HeroSection';
import Title from '../Title';
import ContactSection from '../ContactSection';
import SocialSection from '../SocialSection';
import BioSection from '../BioSection';
import GaleryCards from '../GaleryCards';

import { Link } from "react-scroll";

function Home() {
    Title('Home');
    return (
        <>
            <Link 
            activeClass="active"
            to="herosection"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
            >
                <HeroSection />
            </Link>
            
            <Link 
            activeClass="active"
            to="galerycards"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
            >
                <GaleryCards />
            </Link>

            <Link 
            activeClass="active"
            to="biosection"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
            >
                <BioSection />
            </Link>

            <Link 
            activeClass="active"
            to="contactsection"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
            >
                <ContactSection />
            </Link>

            <Link 
            activeClass="active"
            to="socialsection"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
            >
                <SocialSection />
            </Link>
        </>
    );
};

export default Home;